.container {
  text-align: left;
}
h1 {
  color: #333;
  font-size: 50px;
  letter-spacing: -3px;
}
.movieBox {
  position: relative;
  display: inline-block;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  box-shadow: rgba(99, 99, 99, 0.7) 0px 2px 8px 0px;
  margin-bottom: 20px;
}
.movieBox * {
  color: white;
}
.contents {
  display: flex;
}
.left {
  position: relative;
  font-size: 0;
}
.square {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-top: 45px solid #fa333d;
  border-bottom: 45px solid transparent;
  border-left: 45px solid #fa333d;
  border-right: 45px solid transparent;
}
.rank {
  font-size: 32px;
  font-weight: 500;
  position: relative;
  top: -65px;
  left: -35px;
}
.right {
  width: 100%;
  padding: 10px 30px;
  box-sizing: border-box;
}
.right > h2 {
  font-size: 34px;
}
.list {
  float: left;
  font-size: 24px;
  margin: 0 120px 10px 0;
}
.textbox {
  width: 95%;
  overflow: hidden;
  white-space: normal;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  word-break: keep-all;
}
.overview {
  font-size: 20px;
  letter-spacing: 1.5px;
  line-height: 28px;
}
