.container {
  margin-top: 35px;
}

/* header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
}
.header > h2 {
  font-size: 40px;
  font-weight: 900;
  letter-spacing: -1.5px;
}
.header > a {
  all: unset;
  cursor: pointer;
  border: 2px solid #fa333d;
  color: #fa333d;
  font-weight: bold;
  font-size: 18px;
  text-align: center;
  padding: 5px 15px;
  align-self: center;
  border-radius: 20px;
}
.header > a:hover {
  background-color: #fa333d;
  color: white;
}

/* movie */
.movieContainer {
  padding-top: 15px;
  box-sizing: border-box;
  height: 400px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}
.movie {
  position: relative;
  padding: 0 20px;
  box-sizing: border-box;
  transition: 0.2s;
}
.movie:hover {
  transform: translateY(-10px);
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.movie h2 {
  font-size: 18px;
}
