.resultPage {
  padding: 0 20px;
  width: 98%;
}
.resultPage > button {
  display: block;
  margin: 30px auto;
}
.title {
  font-size: 30px;
  letter-spacing: 1px;
  margin-bottom: 50px;
}
.title > strong {
  font-size: 40px;
  vertical-align: text-bottom;
}
.noResult {
  text-align: center;
  font-size: 30px;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -45%);
}
.result {
  display: grid;
  grid-template-columns: repeat(6, 200px);
  justify-content: space-around;
  text-align: center;
}
.resultBox {
  min-width: 200px;
  margin: 0 20px 20px 0;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
