.container {
  text-align: center;
  width: 100%;
  height: 80vh;
  position: relative;
}
.wrap {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-45%, -45%);
}
input {
  all: unset;
  width: 500px;
  border-bottom: 2px solid #ccc;
  padding: 10px 5px;
  box-sizing: border-box;
  font-size: 30px;
  margin: 0 20px 30px 0;
}
input:focus {
  border-bottom: 2px solid #fa333d;
}
button {
  all: unset;
  cursor: pointer;
  background-color: #fa333d;
  font-size: 30px;
  padding: 10px 20px;
  box-sizing: border-box;
  color: white;
}
ul {
  position: relative;
  left: -10%;
}
li {
  cursor: pointer;
  list-style: none;
  font-size: 24px;
  line-height: 50px;
}
