.container {
  width: 100%;
  height: 95vh;
  max-height: 1000px;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.container > div {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.bg {
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
}

/* content */
.content {
  z-index: 100;
}
.content > * {
  width: 90%;
  margin: 0 auto;
}
.header {
  height: 130px;
  margin: 10px auto 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header h3 {
  font-size: 50px;
  font-weight: 700;
  color: white;
}
.btnWrap {
  display: flex;
}
.btnWrap button {
  all: unset;
  cursor: pointer;
  border: 1px solid white;
  border-radius: 50%;
  padding: 10px;
  margin-left: 10px;
}
.btnWrap img {
  display: block;
  width: 30px;
}

.columns {
  display: flex;
  color: white;
}
.columns * {
  color: white;
}

/* Left Content */
.left {
  width: 16.5%;
}
.left h2 {
  font-size: 28px;
  padding-left: 10px;
}
.left li {
  list-style: disc;
  line-height: 36px;
}

/* Right Content */
.right {
  background-color: rgba(0, 0, 0, 0.8);
  width: 83.5%;
  height: auto;
  padding: 30px;
  padding-bottom: 0;
}
.mediumText {
  font-size: 22px;
  margin: 15px 0;
}
.first {
  margin: 30px 0;
}
.mediumText strong {
  font-size: 24px;
}
.mediumText > p {
  line-height: 35px;
  letter-spacing: 1px;
}
.vertical {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
